/* Global reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Center container */
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f8ff; /* Light blue background color */
}

/* Container styling */
.registration-container {
    max-width: 400px;
    align-content: center;
    padding: 20px;
    border: 7px solid #ccc;
    border-radius: 14px;
    background-color: #a4d2d7; /* White background color */
    box-shadow: 0 0 10px rgba(62, 18, 18, 0.1);
    margin: 20px; /* Adjust the margin to create space between login and registration containers */
    margin-left: 100px;
    margin-right: 0cqmin;
    align-items: center;
    justify-content: center; 
}

/* Title styling */
.registration-container h2 {
    text-align:center;
    color: #2f346a;
}

/* Form styling */
.registration-container form {
    margin-top: 0px;
    
}

.registration-container label {
    display: block;
    margin-bottom: 7px;
    color: #574895;
}

.registration-container input[type="text"],
.registration-container input[type="password"] {
    width: 100%;
    padding: 15px;
    margin-bottom: 15px;
    border: 6px solid #5686b8;
    border-radius: 50px;
}

/* Button styling */
.registration-container button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fafcfd;
    border: none;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
}

.registration-container button:hover {
    background-color: #6aaaee;
}

/* Registration link styling */
.registration-container h4 {
    text-align: center;
    margin-top: 39px;
    font-size: xx-large;
    color: #6260b8;
}

.registration-container h4 a {
    color: #e7eff8;
    text-decoration: none;
}

.registration-container h4 a:hover {
    text-decoration: underline;
}
